<template>
  <main v-if="content && similar" class="">
    <div class="container d-flex flex-wrap justify-content-between">
      <div class="serchBox w-100 text-center">
        <SectionTital
          :title="true"
          class="recipe-tital"
          :subtitle="true"
          :subtitleText="`Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore`"
          :type="'main-tital'"
          :titalText="`Blog & Article`"
        ></SectionTital>
        <div class="serch-for d-flex mt-5 me-auto ms-auto">
          <input
            required
            v-model="SerchInput"
            type="text"
            placeholder="Search article, news or recipe..."
            class="flex-grow-1 bg-transparent"
            name=""
            id=""
          />
          <TheBotton
            @click="serchBox"
            :type="'primary'"
            class="p-3 d-flex ms-auto align-items-center justify-content-center serch-button"
          >
            <template #Text>
              <p class="m-0 me-1">Serch</p>
            </template>
          </TheBotton>
        </div>
      </div>

      <section class="articles">
        <RouterLink
          v-for="article in content.articles.slice(
            startBloge,
            startBloge + articlesPerPage
          )"
          :key="article.source.name"
          :to="{
            name: 'ArticleVue',
            query: {
              title: article.title,
              publishedAt: article.publishedAt.slice(0, 10),
              author: article.author,
            },
            params: { id: article.title },
          }"
        >
          <article class="d-flex mb- flex-wrap justify-content-between">
            <div
              style="
                position: absolute;
                top: 50%;
                background-color: blue;
                color: white;
              "
            ></div>
            <header>
              <img class="w-100 h-100" :src="article.urlToImage" alt="" />
            </header>
            <section class="py-3 ps-3">
              <h4 :title="article.title" class="primary-color bold-text">
                {{ article.title.slice(0, 30) }}...
              </h4>
              <p class="description sub-text">{{ article.description }}</p>
              <div>
                <img src="" alt="" />
              </div>
              <div class="d-flex author-publishedAt">
                <p class="me-2 primary-color bold-text" v-if="article.author">
                  {{
                    article.author.slice(
                      0,
                      article.author.indexOf(",") || article.author.length - 1
                    )
                  }}
                </p>
                <p class="sub-text">{{ article.publishedAt }}</p>
              </div>
            </section>
          </article>
        </RouterLink>
        <div v-if="content.articles.length === 0" class="found-nothing">
          <i class="fa-regular fa-face-kiss"></i>
          <p>ther is nothing</p>
        </div>
      </section>
      <aside>
        <SectionTital
          :title="true"
          class="aside-tital mb-4"
          :subtitle="false"
          :type="'small-tital'"
          :titalText="'Tasty Recipes'"
        ></SectionTital>
        <div
          class="recipe d-flex align-items-center p-3"
          v-for="obj in similar.recipes.slice(0, 4)"
          :key="obj.id"
        >
          <div class="recipe-img">
            <img width="100%" :src="obj.image" alt="" />
          </div>

          <h4 class="ps-2 primary-color">{{ obj.title }}</h4>
        </div>
      </aside>
      <section class="pagination-nav w-100">
        <nav aria-label="Page navigation example">
          <ul class="pagination my-auto justify-content-center flex-wrap">
            <li class="page-item mb-2">
              <a
                @click.prevent="prevPage"
                v-if="a[a.length - 1] > 6"
                class="page-link d-flex align-items-center justify-content-center"
                href="#"
                ><i class="fa-solid fa-chevron-left"></i
              ></a>
            </li>
            <li v-for="number in a" :key="number" class="page-item mx-2 mb-2">
              <a
                @click.prevent="showArticles"
                class="page-link primary-color d-flex align-items-center justify-content-center"
                href="#"
                >{{ number }}</a
              >
            </li>
            <li class="page-item mx-2 mb-2">
              <a
                @click.prevent="SeeMoreResults"
                class="page-link primary-color d-flex align-items-center justify-content-center"
                href="#"
                >...</a
              >
            </li>
            <li class="page-item mb-2">
              <a
                ref="nextpageB"
                @click.prevent="nextpage"
                class="page-link primary-color d-flex align-items-center justify-content-center"
                href=""
                ><i class="fa-solid fa-chevron-right"></i
              ></a>
            </li>
          </ul>
        </nav>
      </section>
    </div>
  </main>
  <FooterCom></FooterCom>
</template>
<script setup>
import { onMounted, ref } from "vue";
import { useHead } from "@vueuse/head"; // استيراد useHead
useHead({
  title: `Blog & Article  `,
  meta: [
    {
      name: "description",
      content:
        "Explore a wide range of insightful blog posts and articles. Discover the latest trends, expert opinions, and in-depth analysis on various topics. Search and browse through articles that cater to your interests and knowledge.",
    },
    {
      name: "keywords",
      content:
        "blog, articles, search blog, browse articles, latest trends, expert opinions, in-depth articles, blog posts, news articles, article categories, tech blogs, lifestyle articles, entertainment blogs, health articles, educational blogs",
    },
  ],
});
let content = ref(null);
const a = ref([1, 2, 3, 4, 5]);
const pages = ref(null);
const nextpageB = ref(null);
let startBloge = ref(0);

let activeLinke = null;
const articlesPerPage = 4; // عدد العناصر في كل صفحة
const similar = ref(null);
const showArticles = (e) => {
  // التحقق إذا كان النص المدخل عددًا
  if (isNaN(e.target.innerText)) {
    return;
  } else {
    // إزالة الصنف "active" من الرابط الحالي
    if (activeLinke) {
      activeLinke.classList.remove("active");
    }

    // تعيين الصنف "active" للعنصر الجديد
    e.target.parentElement.classList.add("active");
    activeLinke = e.target.parentElement;

    // حساب الفهرس لبداية المقالات في الصفحة
    startBloge.value = (+e.target.innerText - 1) * articlesPerPage;
  }
};
const nextpage = () => {
  if (
    activeLinke &&
    +activeLinke.nextSibling.firstChild.innerText !==
      a.value[a.value.length - 1] &&
    +activeLinke.nextSibling.firstChild.innerText >= a.value[a.value.length - 1]
  ) {
    activeLinke.classList.remove("active");
    activeLinke = activeLinke.nextSibling;
    activeLinke.classList.add("active");

    return (startBloge.value =
      (+activeLinke.nextSibling.firstChild.innerText - 1) * articlesPerPage);
  } else if (a.value[a.value.length - 1] < pages.value / 5) {
    let arr = [];
    for (
      let index = a.value[a.value.length - 1];
      index < a.value[a.value.length - 1] + 5;
      index++
    ) {
      arr.push(index);
    }
    activeLinke.classList.remove("active");
    activeLinke = activeLinke.nextSibling;
    activeLinke.classList.add("active");
    a.value = arr;
  } else if (
    +activeLinke.nextSibling.firstChild.innerText ===
    a.value[a.value.length - 1]
  ) {
    activeLinke.classList.remove("active");
    activeLinke = activeLinke.nextSibling;
    activeLinke.classList.add("active");
    nextpageB.value.disabled = true;
    return (startBloge.value =
      (+activeLinke.firstChild.innerText - 1) * articlesPerPage);
  }
};
const prevPage = () => {
  if (
    activeLinke &&
    activeLinke.previousSibling &&
    +activeLinke.firstChild.innerText !== a.value[0]
  ) {
    activeLinke.classList.remove("active");
    activeLinke = activeLinke.previousSibling;
    activeLinke.classList.add("active");

    startBloge.value =
      (+activeLinke.firstChild.innerText - 2) * articlesPerPage;
  } else if (a.value[0] > 1) {
    let arr = [];
    for (let index = a.value[0]; index > a.value[0] - 5; index--) {
      arr.unshift(index);
    }
    //activeLinke.classList.remove("active");

    setTimeout(() => {
      activeLinke = activeLinke.parentElement.children[5];
      console.log(activeLinke);
    }, 0);
    console.log(
      activeLinke.firstChild.innerText,
      activeLinke.parentElement.children[5]
    );
    activeLinke.classList.add("active");
    a.value = arr;

    return (startBloge.value =
      (+activeLinke.firstChild.innerText - 1) * articlesPerPage);
  }
};
const http = async () => {
  try {
    let response = await fetch(
      `https://newsapi.org/v2/everything?q=food+chef+guide&apiKey=1e4f3628160f4bfb8d6e380d56aa703c`
    );
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    content.value = await response.json();
    pages.value = content.value.articles.length;
  } catch (error) {
    console.error(`Error fetching random recipes: ${error.message}`);
  }
};
http();

const SeeMoreResults = () => {
  if (a.value[a.value.length - 1] < Math.ceil(pages.value / 5)) {
    console.log(a.value[a.value.length - 1]);
    let arr = [];
    for (
      let index = a.value[a.value.length - 1];
      index <= a.value[a.value.length - 1] + 5;
      index++
    ) {
      arr.push(index);
    }
    a.value = arr;
  }
};
const otherRecipe = async () => {
  try {
    let response = await fetch(
      `https://api.spoonacular.com/recipes/random?apiKey=cd216c403a9948fbaab18df612d15354&number=10
`
    );
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    similar.value = await response.json();
    console.log(similar.value);
  } catch (error) {
    console.error(`Error fetching random recipes: ${error.message}`);
  }
};
const SerchInput = ref(null);
const serchBox = async () => {
  if (SerchInput.value) {
    try {
      let response = await fetch(
        `https://newsapi.org/v2/everything?q=${SerchInput.value}&apiKey=1e4f3628160f4bfb8d6e380d56aa703c`
      );
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      content.value = await response.json();
      pages.value = content.value.articles.length;
      console.log(content.value.articles, pages.value);
      if (pages.value <= 5) {
        a.value = [];
        for (let i = 1; i < Math.ceil(pages.value / articlesPerPage); i++) {
          a.value.push(i);
        }
        console.log(a.value);
      } else {
        a.value = [1, 2, 3, 4, 5];
      }
    } catch (error) {
      console.error(`Error fetching random recipes: ${error.message}`);
    }
  }
};

otherRecipe();
onMounted(() => {
  if (content.value) {
    console.log(`ss`);
  }
});
</script>
<style lang="scss" scoped>
main {
  margin-bottom: 10rem;
  div.container {
    div.serchBox {
      margin-bottom: 5rem;
      div.serch-for {
        border-radius: 25px;
        max-width: 43.75rem;
        padding: 10px;
        border: 1px solid var(--border);
        height: 5rem;
        [type="text"] {
          color: var(--text-color);
        }
        .serch-button {
          width: 10rem;
        }
      }
    }
    .articles {
      article {
        margin-bottom: 2rem;
        header {
          img {
            border-radius: 25px;
          }
        }
        max-width: 52.5rem;
        header {
          width: 18.13rem;
          height: 12.5rem;
        }
        section {
          p.description {
            max-width: 31.88rem;
          }
        }
      }
    }
    .found-nothing {
      display: flex;
      flex-direction: column;
      position: relative;
      align-items: center;
      justify-content: center;
      left: 50%;
      i {
        font-size: 5rem;
      }
      p {
        font-size: 4rem;
        text-transform: capitalize;
        font-weight: bold;
      }
    }
    aside {
      .aside-tital {
        * {
          font-weight: bold;
        }
      }
      max-width: 25rem;
      div.recipe {
        width: 25rem;
        height: 7.5rem;
        .recipe-img {
          width: 9.25rem;
          height: 7.5rem;
          img {
            border-radius: 1.25rem;
          }
        }
        h4 {
          font-size: 1.25rem;
          max-width: 12.25rem;
        }
      }
    }
    section.pagination-nav {
      margin-top: 5rem;
      li.page-item {
        &.active {
          a {
            background-color: var(--text-color);
            color: var(--primary);
            box-shadow: none;
            border: none;
          }
        }
        a {
          background-color: var(--primary);
          color: var(--text-color);
          width: 4rem;
          height: 4rem;
          border-radius: 1rem;
          transition: 0.3s;
          &:hover {
            background-color: #ddd;
            color: var(--primary);
          }
        }
      }
    }
  }
}
[data-theme="dark"] {
  div.container {
    section.pagination-nav {
      li.page-item {
        a {
          border: none;
        }
      }
    }
  }
}
@media (max-width: 1400px) {
  main {
    margin-bottom: 10rem;
    div.container {
      aside {
        display: flex;
        flex-wrap: wrap;
        min-width: 100%;
        justify-content: space-between;
        .aside-tital {
          flex-basis: 100%;
        }
      }
    }
  }
}
@media (max-width: 768px) {
  main {
    margin-bottom: 10rem;
    div.container {
      .articles {
        article {
          align-items: center;
          justify-content: center !important;
          text-align: center;
          div.author-publishedAt {
            justify-content: center;
          }
        }
      }
      div.serchBox {
        div.serch-for {
          .serch-button {
            width: 5rem;
          }
        }
      }
    }
  }
}
</style>
